import Msg from './hello/index'

const components = [
  Msg
]
const install = function (Vue, opts = {}) {
  components.map(component => {
    Vue.component(component.name, component)
  })
  const ELEMENT = {}
  ELEMENT.size = opts.size || ''
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}
export default {
  version: '1.0.0',
  install,
  Msg
}



